import React from "react";
import "../styles/navbar.css";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import { useHistory } from "react-router-dom";
function Navbar(props) {
  const history = useHistory();
  const backToMenu = () => {
    localStorage.removeItem("activePlayers");
    localStorage.removeItem("matrix");
    localStorage.removeItem("gridSize");
    localStorage.removeItem("playerCount");
    localStorage.removeItem("isGameInProgress");
    localStorage.removeItem("winner");
    history.push("/");
  };
  return (
    <div className="navbar_custom">
      <div
        className="navbar__heading b"
        onClick={backToMenu}
        // onClick={() => (window.location.href = "/")}
      >
        <span>
          <ArrowBackIosIcon />
          {props.error ? "ERROR" : "Chain Reaction"}
        </span>
      </div>
      <div className="navbar__heading">{props.screenName}</div>
    </div>
  );
}

export default Navbar;
